@use "../util" as *;

html {
    font-size: 100%;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}

body {
    font-family: var(--font-Commissioner);
    font-size: rem(16);
    background-color: #f3f3f3;
    color: var(--black);
}