:root {
  --font-Commissioner: "Commissioner", sans-serif;
}

:root {
  /* Primary */
  --moderateCyan: hsl(176, 50%, 47%);
  --darkCyan: hsl(176, 72%, 28%);
  /* Neutral */
  --black: hsl(0, 0%, 0%);
  --darkGray: hsl(0, 0%, 28%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-Commissioner);
  font-size: 1rem;
  background-color: #f3f3f3;
  color: var(--black);
}